import React, { Component } from 'react';
import SwapiService from '../../services/swapi-service'
import './person-details.css';
import Spinner from '../spinner/spinner';
import ErrorButton from '../error-btn'

export default class PersonDetails extends Component {

  swapi = new SwapiService();

  state = {
    person: null,
    loading: false

  }

  componentDidMount() {
    // this.updatePerson();
  }

  componentDidUpdate(prevProps) {
    if (this.props.id !== prevProps.id) {
      this.updatePerson();
    }
  }

  updatePerson() {
    this.setState({
      loading: true
    })
    const { id } = this.props;
    if (!id)
      return;

    this.swapi.getPerson(id)
      .then((person) => {
        this.setState({
          person,
          loading: false
        })
      });
  }

  render() {

    const { loading, person } = this.state;

    const spinner = loading ? <Spinner /> : null;

    const isPersonNotSelected = !person ?
      <span>Selected Person From List!</span> :
      null;
    if (isPersonNotSelected) return isPersonNotSelected;

    const personDetails = (!loading && person) ? personDetailsView(this.state.person) : null;

    return (
      <div className="person-details card">
        {spinner}
        {isPersonNotSelected}
        {personDetails}
      </div>
    )
  }
}

const personDetailsView = ({ id, name, gender, birthYear, eyeColor }) => {
  return (<React.Fragment>
    <img className="person-image"
      src={`https://starwars-visualguide.com/assets/img/characters/${id}.jpg`} />

    <div className="card-body">
      <h4>{name}</h4>
      <ul className="list-group list-group-flush">
        <li className="list-group-item">
          <span className="term">Gender</span>
          <span>{gender}</span>
        </li>
        <li className="list-group-item">
          <span className="term">Birth Year</span>
          <span>{birthYear}</span>
        </li>
        <li className="list-group-item">
          <span className="term">Eye Color</span>
          <span>{eyeColor}</span>
        </li>
      </ul>
      <ErrorButton/>
    </div>
  </React.Fragment>
  )
}


